.contactContainer {
  background: #171717;
  height: auto;
}

#apple {
  width: 100%;
  /*background: linear-gradient(45deg, #353535, black);*/
  background-color: #0E0E0E;
  border-radius: 5px 0px 5px 0px;
  padding: 10% 10% ;
}

#a2 {
  position:absolute;
  left: 0.5rem;
  width: 0.2rem;
  bottom: 0;
  height: 100vh;
  background: linear-gradient(45deg, #4D2C49, #AF30A3 45%, #7C12AE);
}
.contactSectionA {

  display: flex;
  max-height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10%;

}

.contactSectionB {

  max-height: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10%;
}

.cont_input {

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 5%;
}

label {
  margin-right: 5%;
  margin-bottom: 5%;
  font-size: clamp(12px, 16px, 20px);
  font-weight: bold;
  color: white;


}

input {
  background-color: rgba(32,32,32,0.5);
  border-style: solid;
  border-color: #9F9F9F;
  min-height: 35px;
  height: 100%;
  min-width: 200px;
  width: 100%;

  color: white;
  font-size: clamp(12px,14px,14px);
}

.submit_cont {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

button {
  margin-top: 10%;
  color: white;
  border-color: white;
  border-style: solid;
  background-color: rgba(255,0,0,0);

  height: 4.5rem;
  width: 100%;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
}

button:hover {
  color: black;
  background-color: white;
}


#input_zip {
  max-width: 75px;
}
